.calculator {
    width: 300px;
    margin: 0 auto;
    border: 2px solid #000;
    background-color: navy;
    border-radius: 10px;
    box-shadow: 5px 5px 5px #888;
  }
  
  .display {
    background-color: navy;
    color: white;
    text-align: right;
    padding: 10px;
    font-size: 24px;
    border-radius: 10px 10px 0 0;
    height: 100px;
  }
  
  .keypad {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
    padding: 20px;
    background-color: black;
  }
  
  .row {
    display: flex;
    justify-content: space-between;
    padding-bottom: 2px;
  }
  
  button {
    width: 100%;
    padding: 15px;
    background-color: black;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 20px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #333;
  }

  .row1 {
    display: flex;
    /* justify-content: space-between; */
    height: 40px;
  }
  